
.product-bom-list {
  height: 100%;
  padding-left: 210px;
}
.product-bom-list .content-holder {
    height: calc(100% - 60px);
    overflow: auto;
}
.product-bom-list .content-holder .content {
      width: 100%;
}
.product-bom-list .content-holder .content .part {
        margin: 10px auto;
        width: 950px;
        background-color: #353945;
        padding: 20px 40px;
}
.product-bom-list .content-holder .content .top {
        margin: 10px auto;
        width: 950px;
        background-color: #353945;
        padding: 20px 40px;
}
.product-bom-list .content-holder .content .top .art-no {
          display: inline-block;
          width: 200px;
          margin-left: 20px;
          color: #A1A5B1;
          line-height: 30px;
}
.product-bom-list .content-holder .content .top .img-div {
          margin-top: 20px;
          text-align: center;
}
.product-bom-list .content-holder .content .top .img-div .upload-icon {
            width: 102px;
            height: 102px;
            margin: 15px;
}
.product-bom-list .content-holder .content .top .img-div .img {
            max-width: 400px;
            max-height: 300px;
            margin: 15px;
}
.product-bom-list .content-holder .content .top .img-div .no-img {
            padding: 60px;
}
.product-bom-list .content-holder .content .list .list-title {
        display: inline-block;
        height: 30px;
        vertical-align: top;
}
.product-bom-list .content-holder .content .list .search-input {
        width: 200px;
        margin-left: 30px;
}
.product-bom-list .content-holder .content .list .bom-list {
        margin-top: 15px;
        width: 100%;
        overflow: auto;
        /*padding-bottom: 60px;*/
        border: 1px solid #2B2F3B;
}
.product-bom-list .content-holder .content .list .bom-list thead td {
          font-size: 12px;
          padding: 10px;
          text-align: center;
          white-space: nowrap;
          color: #A1A5B1;
}
.product-bom-list .content-holder .content .list .bom-list tr {
          height: 30px;
}
.product-bom-list .content-holder .content .list .bom-list tr:nth-child(2n+1) {
            background: rgba(39, 43, 55, 0.2);
            /*box-shadow: 1px 0 0 1px #2B2F3B;*/
}
.product-bom-list .content-holder .content .list .bom-list tr td {
            font-size: 12px;
            padding: 0 5px;
            text-align: center;
            max-width: 150px;
            overflow: hidden;
            text-overflow: ellipsis;
}
.product-bom-list .content-holder .content .list .bom-list tr td img {
              vertical-align: middle;
              width: 20px;
              height: 20px;
}
.product-bom-list .content-holder .content .list .bom-list tr td .item-input {
              width: 40px;
}
.product-bom-list .content-holder .content .list .bom-list tr td .item-input .el-input__inner {
                text-align: center;
                height: 20px;
                vertical-align: middle;
}
.product-bom-list .large-image {
    text-align: center;
}
.product-bom-list .large-image .el-dialog--large {
      width: auto;
}
.product-bom-list .large-image .el-dialog__header {
      display: none;
}
.product-bom-list .large-image .el-dialog__body {
      font-size: 0;
      width: auto;
      max-width: 1000px;
      padding: 0;
      background-color: white;
}
.product-bom-list .large-image .el-dialog__body .img {
        max-width: 1000px;
        height: auto;
}
.product-bom-list .dialogAddItem .el-dialog--large {
    width: 1100px;
}



