
.login {
  width: 100%;
  height: 100%;
  min-width: 350px;
  min-height: 500px;
}
.login .login-header {
    overflow: hidden;
}
.login .login-header .language {
      margin-top: 18px;
      float: right;
      margin-right: 30px;
      cursor: pointer;
}
.login .login-header .language .title {
        text-align: center;
}
.login .login-header .language .icon {
        margin: 10px;
}
.login .login-header .language .icon .img-icon {
          border-radius: 50%;
          margin: 0 4px;
          width: 60px;
          height: 60px;
          display: inline-block;
          text-align: center;
}
.login .login-header .language .icon .img-icon img {
            margin-top: 1px;
            border-radius: 50%;
            width: 58px;
            height: 58px;
}
.login .login-header .language .icon .img-icon.active, .login .login-header .language .icon .img-icon:hover {
            background: #E26829;
}
.login .login-header .language .text {
        margin-left: 10px;
}
.login .login-header .language .text .label {
          text-align: center;
          display: inline-block;
          width: 68px;
}
.login .main-container {
    height: calc(100% - 300px);
    position: relative;
}
.login .main-container .main-warp {
      overflow: auto;
      position: absolute;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%);
      text-align: center;
}
.login .main-container .main-warp .head {
        font-size: 0;
        margin: 0 auto;
        border-radius: 50%;
        width: 140px;
        height: 140px;
        background: white;
        overflow: hidden;
}
.login .main-container .main-warp .head .icon-login {
          width: 140px;
          height: 140px;
}
.login .main-container .main-warp .head .icon-login .logo-img {
            position: relative;
            top: 70px;
            -webkit-transform: translate(0, -50%);
                    transform: translate(0, -50%);
            width: 100%;
            height: auto;
}
.login .main-container .main-warp .login-text {
        font-size: 18px;
        margin-top: 20px;
        margin-bottom: 32px;
}
.login .main-container .main-warp .username, .login .main-container .main-warp .password {
        width: 240px;
        font-size: 12px;
        display: block;
        left: 50%;
        margin-left: -120px;
}
.login .main-container .main-warp .username > input, .login .main-container .main-warp .password > input {
          text-align: center;
          height: 40px;
}
.login .main-container .main-warp .username > input {
        border-radius: 15px 15px 0 0;
        font-size: 14px;
}
.login .main-container .main-warp .password {
        margin-top: 1px;
}
.login .main-container .main-warp .password > input {
          border-radius: 0 0 15px 15px;
          font-size: 14px;
}
.login .main-container .main-warp .check-div {
        overflow: hidden;
        margin-top: 24px;
}
.login .main-container .main-warp .check-div .remember-text {
          float: left;
          margin-left: 9px;
          font-size: 12px;
}
.login .main-container .main-warp .check-div .forget {
          color: #858890;
          margin-left: 30px;
          float: right;
          text-decoration: underline;
          font-size: 12px;
}
.login .main-container .main-warp .check-div .forget:hover {
            color: #00ffff;
}
.login .main-container .main-warp .check-div .check-item {
          float: left;
          background: #2B2F3B;
          width: 16px;
          height: 16px;
          border-radius: 50%;
}
.login .main-container .main-warp .check-div .check-item .icon-check {
            width: 8px;
            height: 8px;
            background: #E26829;
            border-radius: 50%;
            position: relative;
            left: 4px;
            top: 4px;
}
.login .main-container .main-warp .login-btn {
        height: 29px;
        width: 240px;
        margin-top: 27px;
}
.login .main-container .main-warp .login-btn span {
          font-size: 16px;
}
.login .main-container .main-warp .sign {
        margin-top: 15px;
        font-size: 12px;
        text-decoration: underline;
}
.login .main-container .main-warp .sign:hover {
          color: #bfcbd9;
}



