
.dealer-invite {
  height: 100%;
}
.dealer-invite .upload-contact {
    display: inline-block;
}
.dealer-invite .status-label {
    color: #A1A5B1;
    font-size: 12px;
    margin-right: 15px;
}
.dealer-invite .search-input {
    width: 160px;
    margin-left: 15px;
}
.dealer-invite .el-transfer-panel__filter .el-input__inner {
    height: 30px;
}
.dealer-invite .transfer {
    margin: 0 auto;
    width: 600px;
}
.dealer-invite .upload {
    height: 60px;
    width: 122px;
    display: inline-block;
}
.dealer-invite .transfer-footer {
    margin-left: 20px;
}
.dealer-invite table {
    border-collapse: collapse;
    border-color: rgba(255, 255, 255, 0.3);
    width: 100%;
}
.dealer-invite table tr td {
      width: 25%;
      text-align: center;
      height: 36px;
      line-height: 36px;
      overflow: hidden;
      text-overflow: ellipsis;
      word-break: break-all;
}
.dealer-invite table tr td input {
        height: 34px;
        background: #2B2F3B;
        width: 100%;
        padding: 0 10px;
        color: #ffffff;
}



