
.edit-category {
  width: 100%;
  height: 100%;
}
.edit-category .header {
    line-height: 60px;
    height: 60px;
    padding: 0 30px;
    border-bottom: 1px solid #2B2F3B;
}
.edit-category .content {
    height: calc(100% - 60px);
    background: #353945;
    overflow: auto;
}
.edit-category .content .label {
      font-size: 16px;
      color: #A1A5B1;
      padding: 9px 33px;
      display: inline-block;
}
.edit-category .content .category-tree {
      margin-left: 34px;
}



