.header {
    position: relative;
    height: 60px;;
    line-height: 60px;
    padding: 0 40px;
    border-bottom: 1px solid #2B2F3B;
    background: #383d49;
    .title {
        //margin-left: 40px;
        font-size: 14px;
        color: #fff;
        //line-height: 60px;
        margin-right: 15px;
    }
    button + .title {
        margin-left: 15px;
    }
    .el-button + .el-button {
        margin-left: 15px;
    }
    .btn-group {
        position: absolute;
        height: 30px;
        right: 40px;
        top: 0;
        width: auto;
    }
}

.content-wrap {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    margin-top: 60px;
    padding-top: 20px;
    overflow-y: auto;
    .content {
        width: 950px;
        margin: 0 auto;
    }
}

// 
.table-wrap {
    width: 100%;
    height: calc(100% - 60px);
    overflow: auto;
    .table-content {
        width: 100%;
        overflow: auto;
        padding-bottom: 60px;
        thead td {
            height: 50px;
            padding: 10px 20px;
            text-align: center;
            white-space: nowrap;
            font-size: 14px;
            color: #A1A5B1;
        }
        tr {
            height: 50px;
            &:nth-child(2n + 1) {
                background: rgba(39, 43, 55, 0.20);
                box-shadow: 1px 0 0 1px #2B2F3B;
            }
            td {
                padding: 0px 20px;
                text-align: center;
                white-space: nowrap;
                font-size: 14px;
                img {
                    margin-top: 6px;
                    background-color: white;
                    border-radius: 4px;
                    width: 38px;
                    height: 38px;
                }
                &.desc {
                    opacity: 0.5;
                }
            }
        }
        tr.large {
            height: 80px;
            td img {
                margin-top: 0px;
                height: 50px;
                width: 50px;
            }
        }
        tbody tr {
            cursor: pointer;
        }
        tbody tr .no-pointer {
            cursor: default;
        }
    }
    .table-footer {
        z-index: 100;
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 60px;
        padding: 15px 40px;
        background-color: #383d49;
        box-shadow: 1px 0 0 1px #2B2F3B;

        .total {
            position: relative;
            top: 5px;
            display: inline-block;
            padding-left: 5px;
            font-size: 14px;
            color: rgba(255, 255, 255, 0.5);
            font-weight: bold;
        }
        .el-pagination {
            display: inline-block;
        }
    }
    .no-data-content {
        position: absolute;
        bottom: 0;
        width: 100%;
        height: calc(100% - 110px);
        padding-top: 50px;
        font-size: 14px;
        background: #2B2F3B;
        text-align: center;
        color: #A1A5B1;
    }
}

// 
.btn-underline {
    color: #A1A5B1;
    text-decoration: underline;
    cursor: pointer;
    &:hover {
        color: #E26829;
        text-decoration: underline;
    }
}

.el-dialog--small {
    width: 558px;
}

.batch-send-dialog .el-dialog--small {
    width: 600px;
}

.error-dialog {
    width: 700px;
}

.save-ticket-dialog {
    width: 584px;
    .el-dialog__body {
        padding: 30px 40px 0 40px;
    }

    .el-dialog__footer {
        padding: 10px 40px 30px 40px;
    }
}

.video-dialog {
    width: 855px;
    .el-dialog__body {
        padding: 0;
        video {
            width: 100%;
        }
    }
}

.dialog-w-400 {
    width: 400px;
    .el-dialog__body {
        padding-bottom: 0;
    }
}

.edit-dialog, .save-vehicle-dialog {
    width: 855px;
    .el-dialog__body {
        padding: 38px 64px 62px 64px;
        .dialog-container {

        }
        .operation-holder {
            overflow: hidden;
            padding-bottom: 20px;
            border-bottom: 1px solid rgba(255, 255, 255, 0.1);

            h4 {
                float: left;
                font-weight: 500;
                font-size: 18px;
                color: #FFFFFF;
            }
            .btn-group {
                float: right;
            }
        }

        .content-item {
            padding: 20px 0;
            border-bottom: 1px solid rgba(255, 255, 255, 0.1);
            font-size: 0;

            .title {
                font-size: 14px;
                color: rgba(255, 255, 255, 0.6);
                display: inline-block;
                width: 135px;
                vertical-align: top;
            }

            .content {
                display: inline-block;
                font-weight: 600;
                font-size: 14px;
                color: #FFFFFF;

                img {
                    width: 142px;
                    height: 142px;
                }

                .el-input {
                    width: 310px;
                }
            }
        }
    }
    .el-dialog__footer {
        padding: 0 64px 62px 0;
    }
}

.export-form-inline {
    .el-form-item {
        width: 100%;
    }
    .el-form-item__label {
        width: 100px;
        text-align: left;
    }
    .el-form-item__content {
        width: calc(100% - 100px);
    }
}

.save-vehicle-dialog {
    width: 600px;
    .el-dialog__body {
        padding: 38px 64px;
    }
    .el-dialog__footer {
        padding: 0 64px 38px 0;
    }
}

.order-list .el-table {
    .el-table__header-wrapper {
        .el-table__header {
            tr {
                height: 50px;
            }
        }
    }
    .el-table__body-wrapper {
        .el-table__body {
            tbody {
                .el-table__row {
                    height: 50px;
                }
            }
        }
    }
}

.el-tree {
    padding-top: 28px;
    border: none;
    background: transparent;
    & > .el-tree-node {
        &.is-current, &.is-expanded {
            font-size: 14px;
            color: #FFFFFF;
            font-weight: bold;
        }
        .el-tree-node__content {
            height: 50px;
            line-height: 50px;
            &:hover {
                background: #272B37;
            }
            .el-tree-node__label {
                padding-left: 6px;
            }
        }
        .el-tree-node {
            &.is-current {
                font-size: 14px;
                color: #FFFFFF;
                & > .el-tree-node__content .el-tree-node__expand-icon.is-leaf + .el-tree-node__label {
                    color: #E26829;
                    font-weight: bold;
                }
            }
            .el-tree-node__content {
                height: 40px;
                line-height: 40px;
            }
            .el-tree-node__label {
                font-size: 14px;
                color: #FFFFFF;
                padding-left: 6px;
            }
        }
    }
}

.carousel {
    min-width: 635px;
    overflow: auto;
    height: 328px;
    background: #fff;
    .el-carousel {
        width: 635px;
        margin: 0 auto;
        .el-carousel__container {
            width: 100%;
            height: 100%;
        }
        .el-carousel__item .img-box {
            width: 100%;
            height: 100%;
            text-align: center;
            img {
                width: 310px;
                height: 100%;
            }
        }
    }
}

.product-save-container .el-textarea .el-textarea__inner {
    border-radius: 4px;
}

.list-add .content .customer-info .left .item .radio .el-radio__input {
    .el-radio__inner {
        width: 16px;
        height: 16px;
        background-color: #2B2F3B;
        border-color: #2B2F3B;
    }
    .el-radio__inner::after {
        background-color: #E26829;
        width: 8px;
        height: 8px;
    }
}

.table-tr-img {
    background-color: white;
    border-radius: 4px;
}

.el-input-group__prepend {
    .el-select {
        .el-input {
            .el-input__inner {
                width: 80px;
                text-align: center;
            }
        }
    }
}

.el-submenu__title {
    font-weight: bold;
}

// collapse style begin
.el-collapse {
    border: 1px solid #22252E;

    .el-collapse-item {

        .el-collapse-item__header {
            position: relative;
            padding-left: 30px;
            border-bottom: 1px solid transparent;

            .el-collapse-item__header__arrow {
                position: absolute;
                right: 30px;
                top: 14px;
                cursor: pointer;
                transform: rotate(90deg);
                -webkit-transition: all .3s;
                -moz-transition: all .3s;
                -ms-transition: all .3s;
                -o-transition: all .3s;
                transition: all .3s;
            }
        }

        &.is-active {
            .el-collapse-item__header__arrow {
                transform: rotate(-90deg);
            }
        }

        .el-collapse-item__wrap {
            border-bottom: 1px solid transparent;
            .el-collapse-item__content {
                padding: 0;
            }
        }
    }
}

.el-form--inline .el-form-item__content {
    vertical-align: middle !important;
    width: 240px;
}

.diagnosis-container .el-collapse {
    border: 1px solid #22252E;
    .el-collapse-item {
        &.is-active .el-collapse-item__header {
            border-bottom: 1px solid transparent;
        }
        .el-collapse-item__header {
            height: 60px;
            line-height: 60px;
            border-bottom: 1px solid #22252E;

            .el-collapse-item__header__arrow {
                top: 22px;
            }
        }
    }
}

.scan-input {
    position: relative;
    .el-input__inner {
        border-color: rgba(217, 202, 191, 0.5);
        &:hover {
            border-color: #E26829;
        }
    }
    &:after {
        content: "";
        position: absolute;
        right: 10px;
        top: 4px;
        width: 20px;
        height: 20px;
        background: url("../../assets/images/icon/scan.png") no-repeat center;
        background-size: cover;
    }
    &.scan-input-top-8:after {
        top: 8px;
    }
}

.input-sn {
    position: relative;
    &:after {
        content: "";
        position: absolute;
        right: 10px;
        top: 20px;
        width: 20px;
        height: 20px;
        background: url("../../assets/images/icon/scan.png") no-repeat center;
        background-size: cover;
    }
}

.input-sn .el-input__inner, .title-select .el-input__inner {
    border-color: rgba(217, 202, 191, 0.5);
    padding-right: 30px;
    &:hover {
        border-color: #E26829;
    }
}

// collapse style end

::-webkit-scrollbar {
    width: 14px;
    height: 14px;
}

::-webkit-scrollbar-track-piece {
    background-color: white;
    -webkit-border-radius: 6px;
    scrollbar-3dlight-color: #302D30;
}

::-webkit-scrollbar-thumb:vertical {
    height: 5px;
    background-color: #2B2F3B;
    -webkit-border-radius: 6px;

}

::-webkit-scrollbar-thumb:horizontal {
    height: 5px;
    background-color: #2B2F3B;
    -webkit-border-radius: 6px;

}

::-webkit-scrollbar-thumb:hover {
    border: 1px #E26829 solid;
    //background: #3fb560;
    cursor: pointer;
}

.logo-tip {
    width: 400px;
    height: 142px;
    padding: 30px 40px;
    display: inline-block;
    vertical-align: top;
    color: red;
    label {
        font-size: 12px;
    }
}

.order-finish{
    color: lightgreen;
}

.order-doing{
    color: orangered;
}




