
.sale-add {
  width: 100%;
  height: 100%;
}
.sale-add .content .title {
    display: block;
    margin-top: 15px;
    padding: 9px 33px;
    font-size: 18px;
}
.sale-add .content .info {
    margin-top: 15px;
    background-color: #353945;
    width: 100%;
    /*margin: 22px 0 0 100px;*/
    border-bottom: 1px dotted #2B2F3B;
}
.sale-add .content .info .left .item .required, .sale-add .content .info .right .item .required {
      display: inline-block;
      padding-left: 6px;
      color: red;
}
.sale-add .content .info .left {
      float: left;
      width: 475px;
}
.sale-add .content .info .left .item {
        margin-left: 33px;
        line-height: 60px;
        overflow: hidden;
        height: 60px;
        font-size: 0;
}
.sale-add .content .info .left .item .label {
          display: inline-block;
          width: 110px;
}
.sale-add .content .info .left .item .input {
          margin-left: 8px;
          width: 300px;
}
.sale-add .content .info .left .item .input .el-input-group__prepend .el-select .el-input .el-input__inner {
            width: 90px;
            text-align: center;
}
.sale-add .content .info .left .item .radio {
          margin-left: 10px;
          color: #FFFFFF;
}
.sale-add .content .info .right {
      float: left;
      width: 475px;
}
.sale-add .content .info .right .item {
        margin-left: 33px;
        line-height: 60px;
        overflow: hidden;
        height: 60px;
        font-size: 0;
}
.sale-add .content .info .right .item .label {
          display: inline-block;
          width: 100px;
}
.sale-add .content .info .right .item .input {
          margin-left: 8px;
          width: 310px;
}
.sale-add .content .info .right .tip {
        margin-left: 130px;
        margin-top: -8px;
        color: red;
}
.sale-add .content .info .right .tip .tip-text {
          font-size: 12px;
}
.sale-add .content .customer {
    height: 135px;
}
.sale-add .content .bike {
    height: 255px;
}
.sale-add .content .bike .left .date-div {
      display: inline-block;
      position: relative;
      top: 15px;
      height: 30px;
      left: 10px;
      width: 120px;
      background: #2B2F3B;
      border-radius: 15px;
}
.sale-add .content .bike .left .date-div .date-label {
        position: relative;
        top: -15px;
        padding-left: 20px;
}
.sale-add .content .bike .left .month-div {
      /*margin-left: 10px;*/
      height: 60px;
      width: 310px;
      display: inline-block;
}
.sale-add .content .bike .left .month-div .month-input {
        width: 120px;
}
.sale-add .content .bike .left .month-div .desc {
        position: relative;
        top: -5px;
        display: inline-block;
        vertical-align: top;
        width: 60px;
        height: 60px;
        font-size: 36px;
        text-align: center;
}
.sale-add .content .bike .right .height-item {
      margin-left: 33px;
      height: 120px;
      overflow: hidden;
}
.sale-add .content .bike .right .height-item .label {
        display: inline-block;
        width: 90px;
        position: relative;
        top: -55px;
}
.sale-add .content .bike .right .height-item .input {
        margin-left: 10px;
        margin-top: 15px;
        width: 310px;
}
.sale-add .content .component {
    margin-bottom: 20px;
    overflow: hidden;
}
.sale-add .content .component .component-item {
      overflow: hidden;
}
.sale-add .content .component .component-item .component-header {
        margin-top: 10px;
        overflow: hidden;
}
.sale-add .content .component .component-item .component-header .component-title {
          color: #858890;
          margin-top: 15px;
          padding: 9px 33px;
}
.sale-add .content .component .component-item .component-header .btn-replace {
          float: right;
          margin-right: 40px;
}
.sale-add .content .component .component-item .component-content {
        padding-left: 33px;
        line-height: 60px;
        overflow: hidden;
        height: 60px;
        float: left;
        display: inline-block;
        width: 50%;
        font-size: 0;
}
.sale-add .content .component .component-item .component-content .component-title {
          display: inline-block;
          width: 90px;
}
.sale-add .content .component .component-item .component-content .component-div {
          display: inline-block;
          position: relative;
          top: 15px;
          height: 30px;
          left: 10px;
          width: 310px;
          background: #2B2F3B;
          border-radius: 15px;
}
.sale-add .content .component .component-item .component-content .component-div .component-label {
            position: relative;
            top: -14px;
            padding-left: 26px;
}



