
.brand-index {
  width: 100%;
  height: 100%;
}
.brand-index .operation {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.brand-index .operation .item {
      text-align: center;
      display: inline-block;
      width: 230px;
      height: 150px;
      margin: 15px;
      background-color: #98B42C;
      border-radius: 10px;
}
.brand-index .operation .item .label {
        margin: auto;
        line-height: 150px;
}



