
.dealer-ticket-list {
  height: 100%;
}
.dealer-ticket-list .dz-started {
    display: inline-block;
    width: 120px;
    height: 120px;
}
.dealer-ticket-list .dz-started .dz-message {
      width: 100%;
      height: 100%;
}
.dealer-ticket-list .dz-started .dz-message img {
        width: 120px;
        height: 120px;
}
.dealer-ticket-list .img-list {
    float: left;
}
.dealer-ticket-list .img-list img {
      width: 120px;
      height: 120px;
      margin-right: 10px;
}
.dealer-ticket-list .el-upload-list__item img {
    display: none;
}
.dealer-ticket-list .el-form-item__label {
    color: #A1A5B1;
}



