
.component-order-produce {
  width: 100%;
  height: 100%;
}
.component-order-produce .header {
    line-height: 60px;
    height: 60px;
    padding: 0 38px;
    border-bottom: 1px solid #2B2F3B;
}
.component-order-produce .header .label {
      margin-left: 25px;
      font-size: 16px;
}
.component-order-produce .header .add-btn {
      margin: 20px 0;
      float: right;
      background-color: #858890;
}
.component-order-produce .content-card-item .content-header {
    display: block;
    padding: 10px 43px;
    font-size: 14px;
    height: 50px;
    line-height: 30px;
    font-weight: bold;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.component-order-produce .content-card-item .content-body {
    font-size: 0;
    background: #353945;
    overflow: hidden;
    padding: 20px 60px;
}
.component-order-produce .content-card-item .content-body .item {
      width: 50%;
      display: inline-block;
      padding-left: 60px;
      line-height: 50px;
}
.component-order-produce .content-card-item .content-body .item label {
        display: inline-block;
        width: 130px;
        font-size: 14px;
        color: #A1A5B1;
}
.component-order-produce .content-card-item .content-body .item span {
        font-size: 14px;
}
.component-order-produce .content-card-item .content-body .hardware-item {
      line-height: 50px;
      overflow: hidden;
}
.component-order-produce .content-card-item .content-body .hardware-item .select-holder {
        width: 50%;
        float: left;
        overflow: hidden;
}
.component-order-produce .content-card-item .content-body .hardware-item .select-holder > label {
          display: inline-block;
          width: 120px;
          /*float: left;*/
}
.component-order-produce .content-card-item .content-body .hardware-item .select-holder .el-select {
          /*float: left;*/
          width: calc(100% - 154px);
}
.component-order-produce .content-card-item .content-body .hardware-item .select-holder .tip {
          display: inline-block;
          border-radius: 50%;
          width: 10px;
          height: 10px;
          background: red;
          margin-left: 6px;
}
.component-order-produce .content-card-item .content-body .hardware-item .select-holder .connect {
          background: #76EE00;
}
.component-order-produce .content-card-item .content-body .hardware-item .operation-holder {
        float: right;
}
.component-order-produce .content-card-item .content-body .connect-tip a {
      color: #E26829;
}
.component-order-produce .data-table {
    width: 100%;
    display: inline-block;
    vertical-align: top;
    padding: 20px 60px;
    background: #353945;
    margin-top: 20px;
}
.component-order-produce .data-table .el-form-item__content .scan-input:after {
      top: 8px;
}
.component-order-produce .data-table table {
      border-collapse: collapse;
      border-color: rgba(255, 255, 255, 0.3);
      width: 100%;
}
.component-order-produce .data-table table tr .exist {
        color: red;
}
.component-order-produce .data-table table tr td {
        width: 25%;
        text-align: center;
        height: 36px;
        line-height: 36px;
        overflow: hidden;
        text-overflow: ellipsis;
        word-break: break-all;
}
.component-order-produce .data-table table tr td.scan {
          padding: 0 10px;
          background: #2B2F3B;
}
.component-order-produce .data-table table tr td.scan input {
            padding: 0 20px 0;
            background: #2B2F3B url("../../../assets/images/icon/scan.png") no-repeat center right;
            background-size: 20px 20px;
}
.component-order-produce .data-table table tr td input {
          text-align: center;
          height: 34px;
          background: #2B2F3B;
          width: 100%;
          padding: 0 10px;
          color: #ffffff;
}
.component-order-produce .data-table .btn-group {
      margin-top: 20px;
      text-align: right;
}
.component-order-produce .dialog-container-pic {
    text-align: center;
}
.component-order-produce .dialog-container-pic .img {
      width: 371px;
      height: 217px;
}



