
.component-order-test-report {
  width: 100%;
  height: 100%;
}
.component-order-test-report .header {
    line-height: 60px;
    height: 60px;
    padding: 0 38px;
    border-bottom: 1px solid #2B2F3B;
}
.component-order-test-report .header .label {
      margin-left: 25px;
      font-size: 16px;
}
.component-order-test-report .header .add-btn {
      margin: 20px 0;
      float: right;
      background-color: #858890;
}
.component-order-test-report .content-card-item .save-btn {
    float: right;
    margin-top: 10px;
}
.component-order-test-report .content-card-item .content-header {
    display: block;
    padding: 10px 43px;
    font-size: 14px;
    height: 50px;
    line-height: 30px;
    font-weight: bold;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.component-order-test-report .content-card-item .content-body {
    font-size: 0;
    background: #353945;
    overflow: hidden;
    padding: 20px 0;
}
.component-order-test-report .content-card-item .content-body .item {
      width: 49%;
      display: inline-block;
      padding-left: 60px;
      line-height: 50px;
}
.component-order-test-report .content-card-item .content-body .item label {
        display: inline-block;
        width: 140px;
        font-size: 14px;
        color: #A1A5B1;
}
.component-order-test-report .content-card-item .content-body .item span {
        margin-left: 8px;
        font-size: 14px;
}
.component-order-test-report .content-card-item .content-body .hardware-item {
      padding: 0 60px;
      line-height: 50px;
      overflow: hidden;
}
.component-order-test-report .content-card-item .content-body .hardware-item .select-holder {
        width: 50%;
        float: left;
        overflow: hidden;
}
.component-order-test-report .content-card-item .content-body .hardware-item .select-holder > label {
          display: inline-block;
          width: 120px;
          /*float: left;*/
}
.component-order-test-report .content-card-item .content-body .hardware-item .select-holder .el-select {
          /*float: left;*/
          width: calc(100% - 144px);
}
.component-order-test-report .content-card-item .content-body .hardware-item .select-holder .tip {
          display: inline-block;
          border-radius: 50%;
          width: 10px;
          height: 10px;
          background: red;
          margin-left: 6px;
}
.component-order-test-report .content-card-item .content-body .hardware-item .select-holder .connect {
          background: #76EE00;
}
.component-order-test-report .content-card-item .content-body .hardware-item .input-holder {
        width: 50%;
        float: left;
        overflow: hidden;
}
.component-order-test-report .content-card-item .content-body .hardware-item .input-holder > label {
          float: left;
          display: inline-block;
          width: 120px;
}
.component-order-test-report .content-card-item .content-body .hardware-item .input-holder .el-input {
          float: left;
          width: calc(100% - 124px);
}
.component-order-test-report .content-card-item .content-body .hardware-item .input-holder .fileList {
          line-height: 20px;
          margin-left: 100px;
}
.component-order-test-report .content-card-item .content-body .hardware-item .operation-holder {
        float: right;
}
.component-order-test-report .content-card-item .content-body .hardware-item .operation-holder .resetButton {
          background-color: red;
}
.component-order-test-report table {
    border-collapse: collapse;
    border-color: rgba(255, 255, 255, 0.3);
    width: 100%;
}
.component-order-test-report table tr td {
      width: 25%;
      text-align: center;
      height: 36px;
      line-height: 36px;
      overflow: hidden;
      text-overflow: ellipsis;
      word-break: break-all;
}
.component-order-test-report table tr td input {
        height: 34px;
        background: #2B2F3B;
        width: 100%;
        padding: 0 10px;
        color: #ffffff;
}



