
.upper-attr {
  padding: 10px 0;
}
.upper-attr:first-child {
    padding: 0;
}
.upper-attr .item {
    position: relative;
    height: 60px;
    line-height: 60px;
    font-size: 0;
    overflow: hidden;
}
.upper-attr .item .title {
      display: inline-block;
      padding: 0 20px;
      width: 120px;
      font-size: 12px;
      color: #fff;
      font-weight: 500;
}
.upper-attr .item .desc {
      display: inline-block;
      vertical-align: top;
      width: 638px;
}
.upper-attr .item .desc .el-input {
        width: 310px;
}
.upper-attr .file-item {
    position: relative;
    font-size: 0;
    overflow: hidden;
}
.upper-attr .file-item .title {
      display: inline-block;
      padding: 0 20px;
      width: 120px;
      font-size: 12px;
      color: #fff;
      font-weight: 500;
}
.upper-attr .file-item .desc {
      display: inline-block;
      vertical-align: top;
      width: 638px;
}
.upper-attr .file-item .desc .label {
        display: inline-block;
        width: 110px;
}
.upper-attr .file-item .desc .btn {
        margin-left: 10px;
}
.upper-attr .imgs {
    position: relative;
    font-size: 0;
    overflow: hidden;
}
.upper-attr .imgs .title {
      display: inline-block;
      padding: 0 20px;
      width: 120px;
      font-size: 12px;
      color: #fff;
      font-weight: 500;
}
.upper-attr .imgs .desc {
      display: inline-block;
      vertical-align: top;
      width: 638px;
}
.upper-attr .imgs .desc .img-upload > div {
        float: left;
        height: 50px;
}
.upper-attr .imgs .desc .img-upload > div img {
          width: 50px;
          height: 50px;
          margin-right: 10px;
}
.upper-attr .imgs .desc .img-upload {
        display: inline-block;
}
.upper-attr .imgs .desc .img-upload .upload-icon {
          width: 50px;
          height: 50px;
}



