
.report-container .header .right {
  float: right;
}
.report-container .header .right .print-btn {
    background: #98B42C;
    border-radius: 100px;
}
.report-container .content .list-group .list-group-item {
  height: 50px;
  line-height: 50px;
  position: relative;
  background: rgba(57, 61, 73, 0);
  -webkit-box-shadow: 0 1px 0 0 #2B2F3B;
          box-shadow: 0 1px 0 0 #2B2F3B;
}
.report-container .content .list-group .list-group-item:nth-child(2n) {
    background: rgba(39, 43, 55, 0.2);
    -webkit-box-shadow: 0 1px 0 0 #2B2F3B;
            box-shadow: 0 1px 0 0 #2B2F3B;
}
.report-container .content .list-group .list-group-item .label-title {
    position: absolute;
    left: 0;
    top: 0;
    width: 300px;
    padding-left: 30px;
    font-size: 14px;
    color: #A1A5B1;
}
.report-container .content .list-group .list-group-item .content {
    width: 100%;
    height: 50px;
    line-height: 50px;
    padding-left: 300px;
    font-size: 14px;
    color: #FFFFFF;
}
.report-container .print-content-wrap {
  display: none;
}
.w-100 {
  width: 150px;
}
.inline-block {
  display: inline-block;
}



