
.component-order-save {
  width: 100%;
  height: 100%;
}
.component-order-save .table-wrap {
    width: 100%;
    height: calc(100% - 392px);
    -webkit-box-shadow: 0 -1px #2B2F3B;
            box-shadow: 0 -1px #2B2F3B;
}
.component-order-save .content {
    height: 100%;
    border: 1px solid #2B2F3B;
}
.component-order-save .content .order-info {
      width: 100%;
      height: 272px;
}
.component-order-save .content .order-info .title {
        height: 50px;
        line-height: 50px;
        padding-left: 40px;
        font-size: 14px;
}
.component-order-save .content .order-info .left {
        float: left;
        width: 100%;
        height: 170px;
        background: #353945;
        -webkit-box-shadow: 0 -1px #2B2F3B;
                box-shadow: 0 -1px #2B2F3B;
}
.component-order-save .content .order-info .left .item {
          margin: 2px 0 0 40px;
          line-height: 60px;
          overflow: hidden;
          height: 50px;
}
.component-order-save .content .order-info .left .item .label {
            display: inline-block;
            width: 120px;
}
.component-order-save .content .order-info .left .item .input {
            margin-left: 10px;
            width: 256px;
            border-radius: 0 0 10px 10px;
}
.component-order-save .content .order-info .left .item .input-left {
            margin-left: 10px;
            width: 200px;
}
.component-order-save .content .order-info .left .item .input-right {
            width: 200px;
}
.component-order-save .content .order-info .left .item .input-note {
            margin-left: 10px;
            width: 603px;
}
.component-order-save .content .order-info .parts-list {
        float: left;
        padding: 0 40px;
        font-size: 12px;
        overflow: hidden;
        height: 50px;
        line-height: 50px;
        width: 100%;
        -webkit-box-shadow: 0 -1px #2B2F3B;
                box-shadow: 0 -1px #2B2F3B;
}
.component-order-save .content .order-info .parts-list .labelList {
          font-size: 12px;
}
.component-order-save .content .order-info .parts-list .att-btn {
          margin-left: 46px;
          padding: 6px 9px;
          min-width: 0;
}
.component-order-save .content .order-info .parts-list .labelAdd {
          margin-left: 14px;
          font-size: 12px;
}
.component-order-save .content .footer {
      width: 100%;
      height: 50px;
      line-height: 50px;
      padding-left: 40px;
      -webkit-box-shadow: 0 -1px #2B2F3B;
              box-shadow: 0 -1px #2B2F3B;
}
.component-order-save .content .footer .footer-pagination {
        float: right;
}
.component-order-save .dialog .el-dialog--large {
    width: 1100px;
}
.component-order-save .dialog .header .search-content {
    width: 200px;
}
.component-order-save .dialog .header .search-btn {
    position: absolute;
    top: 15px;
    left: 250px;
    width: 30px;
    height: 30px;
}



