
.brand-list {
  width: 100%;
  height: 100%;
}
.brand-list .td-name {
    max-width: 150px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}
.brand-list .td-img {
    max-width: 80px;
}
.brand-list .desc {
    text-overflow: ellipsis;
    max-width: 570px;
    overflow: hidden;
}



