
.account .nav-btn {
  margin: 18px 34px;
}
.account .main-warp {
  text-align: center;
  position: absolute;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
}
.account .main-warp .head {
    margin-top: 70px;
    height: 80px;
    width: 432px;
    margin-left: auto;
}
