    text-align: center;
}
.account .main-warp .tip {
    margin-top: 16px;
    font-size: 12px;
    color: #858890;
}
.account .main-warp .error {
    color: #EB2226;
}
.account .main-warp .email {
    width: 310px;
    margin-top: 111px;
}
.account .main-warp .email > input {
      padding-left: 66px;
}
.account .main-warp .input {
    width: 310px;
    margin-top: 16px;
}
.account .next {
  margin-top: 36px;
  width: 168px;
}



