
@charset "UTF-8";
.hmi {
  width: 100%;
  height: 100%;
  overflow: auto;
  padding: 44px 0;
}
.hmi .btn-back {
    margin-left: 40px;
    top: -26px;
    position: relative;
}
.hmi .hmi-content {
    width: 950px;
    margin: 0 auto;
}
.hmi .hmi-content .title {
      margin-left: 33px;
      font-size: 16px;
}
.hmi .hmi-content .info {
      overflow: hidden;
      background: #353945;
      width: 950px;
      height: 330px;
      margin-top: 22px;
      /*  Firefox */
      -webkit-box-shadow: 0 1px 0 #2B2F3B;
              box-shadow: 0 1px 0 #2B2F3B;
}
.hmi .hmi-content .info .btn-row {
        height: 30px;
        width: 100%;
        position: relative;
        line-height: 30px;
}
.hmi .hmi-content .info .btn-row .btn-group {
          position: absolute;
          right: 26px;
}
.hmi .hmi-content .info .border-bottom {
        border-bottom: 1px solid #2B2F3B;
}
.hmi .hmi-content .info .info-left {
        float: left;
        margin-top: 9px;
        width: 474px;
        border-right: 1px dotted #2B2F3B;
}
.hmi .hmi-content .info .info-left .item {
          margin-left: 33px;
          height: 60px;
          line-height: 60px;
}
.hmi .hmi-content .info .info-left .item .label {
            width: 110px;
            display: inline-block;
}
.hmi .hmi-content .info .info-left .item .input {
            width: 290px;
}
.hmi .hmi-content .info .info-left .item .input > input {
              height: 29px;
}
.hmi .hmi-content .info .info-left .sn {
          height: 60px;
          margin-left: 33px;
          line-height: 60px;
          width: 410px;
}
.hmi .hmi-content .info .info-left .sn .label {
            width: 90px;
            display: inline-block;
}
.hmi .hmi-content .info .info-left .sn .sn-input {
            width: 150px;
            margin-left: 10px;
}
.hmi .hmi-content .info .info-left .sn .btn-read {
            width: 70px;
            margin-left: 7px;
}
.hmi .hmi-content .info .info-left .sn .btn-write {
            margin-left: 1px;
            height: 29px;
            width: 70px;
}
.hmi .hmi-content .info .info-right {
        float: left;
        margin-top: 9px;
        width: 474px;
}
.hmi .hmi-content .info .info-right .item {
          margin-left: 33px;
          height: 60px;
          line-height: 60px;
          font-size: 0;
}
.hmi .hmi-content .info .info-right .item .el-select {
            width: 175px;
}
.hmi .hmi-content .info .info-right .item .tip {
            display: inline-block;
            border-radius: 50%;
            width: 10px;
            height: 10px;
            background: red;
            margin: 0 18px 0 8px;
}
.hmi .hmi-content .info .info-right .item .connect {
            background: #76EE00;
}
.hmi .hmi-content .info .info-right .item .label {
            width: 120px;
            display: inline-block;
}
.hmi .hmi-content .info .info-right .item .input {
            width: 295px;
}
.hmi .hmi-content .data {
      width: 950px;
      height: 260px;
      background: #353945;
      margin-top: 1px;
}



