
.oem-list {
  width: 100%;
  height: 100%;
}
.oem-list .head {
    height: 60px;
    border-bottom: 1px solid #2B2F3B;
    overflow: hidden;
}
.oem-list .td {
    overflow: hidden;
    text-overflow: ellipsis;
}
.oem-list .name {
    max-width: 120px;
}
.oem-list .address {
    max-width: 280px;
}



