
.ticket-detail {
  height: 100%;
}
.ticket-detail .content-wrap {
    overflow: hidden;
    padding-top: 0;
}
.ticket-detail .content-card-item {
    width: 400px;
    height: 100%;
    float: left;
    border-right: 1px solid #2B2F3B;
    overflow: auto;
}
.ticket-detail .content-card-item .content-header {
      display: block;
      padding: 10px 43px;
      font-size: 14px;
      height: 50px;
      line-height: 30px;
      font-weight: bold;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
}
.ticket-detail .content-card-item .content-body {
      font-size: 0;
      background: #353945;
      overflow: auto;
      padding: 20px 0;
      height: calc(100% - 50px);
}
.ticket-detail .content-card-item .content-body .item {
        width: 100%;
        padding: 0 43px;
        line-height: 50px;
}
.ticket-detail .content-card-item .content-body .item label {
          vertical-align: top;
          display: inline-block;
          width: 100px;
          font-size: 14px;
          color: #A1A5B1;
}
.ticket-detail .content-card-item .content-body .item span {
          font-size: 14px;
}
.ticket-detail .content-card-item .content-body .item .lh-20 {
          width: calc(100% - 104px);
          vertical-align: top;
          padding: 16px 0;
          font-size: 14px;
          display: inline-block;
          line-height: 20px;
}
.ticket-detail .content-card-item .content-body .item .lh-20 img {
            width: 100px;
            height: 100px;
}
.ticket-detail .content-card-item .content-body .item .lh-20 img:nth-child(2n+1) {
              margin-right: 6px;
}
.ticket-detail .content-card-item .content-body .item video {
          width: 100%;
}
.ticket-detail .message-box {
    float: right;
    width: calc(100% - 400px);
    height: 100%;
}
.ticket-detail .message-box .message {
      height: calc(100% - 160px);
      background-color: #2B2F3B;
      padding: 10px 15px;
      overflow-y: auto;
}
.ticket-detail .message-box .message li {
        margin-bottom: 15px;
}
.ticket-detail .message-box .message .time {
        margin: 7px 0;
        text-align: center;
}
.ticket-detail .message-box .message .time > span {
          display: inline-block;
          padding: 0 10px;
          font-size: 12px;
          color: #fff;
          border-radius: 2px;
          /*background-color: #dcdcdc;*/
}
.ticket-detail .message-box .message .avatar-box {
        float: left;
        margin: 0 10px 0 0;
        text-align: center;
}
.ticket-detail .message-box .message .avatar-box .avatar {
          border-radius: 3px;
}
.ticket-detail .message-box .message .avatar-box p {
          font-size: 10px;
          color: #A1A5B1;
}
.ticket-detail .message-box .message .text-box > p {
        font-size: 10px;
        color: #A1A5B1;
        margin-bottom: 2px;
}
.ticket-detail .message-box .message .text {
        display: inline-block;
        position: relative;
        padding: 0 10px;
        max-width: calc(100% - 40px);
        min-height: 30px;
        line-height: 2.5;
        font-size: 12px;
        text-align: left;
        word-break: break-all;
        background-color: #fafafa;
        border-radius: 4px;
        color: #A1A5B1;
}
.ticket-detail .message-box .message .text .text-img img {
          max-width: 500px;
}
.ticket-detail .message-box .message .text:before {
          content: " ";
          position: absolute;
          top: 9px;
          right: 100%;
          border: 6px solid transparent;
          border-right-color: #fafafa;
}
.ticket-detail .message-box .message .self {
        text-align: right;
}
.ticket-detail .message-box .message .self .avatar-box {
          float: right;
          margin: 0 0 0 10px;
}
.ticket-detail .message-box .message .self .text {
          background-color: #b2e281;
          color: #ffffff;
}
.ticket-detail .message-box .message .self .text:before {
            right: inherit;
            left: 100%;
            border-right-color: transparent;
            border-left-color: #b2e281;
}
.ticket-detail .message-box .message-100 {
      height: 100%;
}
.ticket-detail .message-box .reply-holder {
      height: 160px;
      overflow-y: auto;
      padding: 20px 40px 16px 40px;
}
.ticket-detail .message-box .reply-holder .el-form-item {
        margin-bottom: 10px;
}
.ticket-detail .message-box .reply-holder .el-form-item:last-child {
          height: 60px;
          overflow: hidden;
          margin-bottom: 0;
}
.ticket-detail .message-box .reply-holder .upload-action {
        height: 60px;
}
.ticket-detail .message-box .reply-holder .dz-clickable {
        display: inline-block;
        width: 60px;
        height: 60px;
}
.ticket-detail .message-box .reply-holder .dz-clickable .dz-message {
          width: 100%;
          height: 100%;
}
.ticket-detail .message-box .reply-holder .dz-clickable .dz-message img {
            width: 60px;
            height: 60px;
}
.ticket-detail .message-box .reply-holder .img-item {
        float: left;
}
.ticket-detail .message-box .reply-holder .img-item img {
          width: 60px;
          height: 60px;
          margin-right: 6px;
}
.ticket-detail .message-box .el-form-item__label {
      color: #A1A5B1;
}
.ticket-detail .message-box .submit {
      position: absolute;
      right: 0;
      top: 30px;
}
.ticket-detail .history-table {
    width: 100%;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
}
.ticket-detail .history-table tr {
      line-height: 30px;
}
.ticket-detail .history-table .head {
      color: white;
}
.ticket-detail .history-table .history-td {
      padding: 0 6px;
      width: 160px;
      border-bottom: 1px solid rgba(255, 255, 255, 0.1);
      font-size: 12px;
}
.tip-content .tip-avatar {
  width: 100%;
  max-width: 300px;
  height: auto;
  position: relative;
  margin-bottom: 10px;
}
.tip-content .tip-avatar img {
    width: 100%;
    height: auto;
}
.tip-content .tip-avatar .org-name {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 30px;
    background: #98B42C;
    line-height: 30px;
    text-align: center;
    color: #fff;
}
.tip-content .tip-item {
  line-height: 30px;
}
.tip-content .tip-item span.title {
    width: 100px;
    color: #A1A5B1;
    display: inline-block;
}
.tip-content .tip-item span.content {
    color: #ccc;
    display: inline-block;
}



