
@charset "UTF-8";
.battery {
  padding-top: 44px;
  width: 100%;
  height: 100%;
  overflow: auto;
}
.battery .btn-back {
    margin-left: 40px;
    top: -26px;
    position: relative;
}
.battery .content .title {
    margin-left: 33px;
    font-size: 16px;
}
.battery .content .info {
    overflow: hidden;
    background: #353945;
    width: 950px;
    height: 259px;
    margin-top: 22px;
    /*  Firefox */
    -webkit-box-shadow: 0 1px 0 #2B2F3B;
            box-shadow: 0 1px 0 #2B2F3B;
}
.battery .content .info .part {
      float: left;
      margin-top: 24px;
      width: 474px;
      border-right: 1px dotted #2B2F3B;
}
.battery .content .info .part .item {
        margin-left: 33px;
        height: 60px;
}
.battery .content .info .part .item .label {
          width: 100px;
          display: inline-block;
}
.battery .content .info .part .item .input {
          width: 310px;
}
.battery .content .info .part .long .long-label {
        position: relative;
        top: 5px;
        width: 100px;
        display: inline-block;
}
.battery .content .info .part .long .input {
        top: -10px;
}
.battery .content .info .part .sn {
        height: 60px;
        margin-left: 33px;
        width: 410px;
}
.battery .content .info .part .sn .label {
          width: 90px;
          display: inline-block;
}
.battery .content .info .part .sn .sn-input {
          width: 148px;
          margin-left: 10px;
}
.battery .content .info .part .sn .btn-read {
          width: 70px;
          margin-left: 7px;
}
.battery .content .info .part .sn .btn-write {
          margin-left: 1px;
          width: 70px;
}
.battery .content .info .right {
      border-right: none;
      font-size: 0;
}
.battery .content .info .right .com-select {
        width: 186px;
}
.battery .content .info .right .tip {
        display: inline-block;
        border-radius: 50%;
        width: 10px;
        height: 10px;
        background: red;
        margin: 0 10px 0 6px;
}
.battery .content .info .right .connect {
        background: #76EE00;
}
.battery .content .info .right .btn-row {
        height: 30px;
        width: 100%;
        position: relative;
        line-height: 30px;
}
.battery .content .info .right .btn-row .btn-group {
          position: absolute;
          right: 30px;
}
.battery .content .data {
    height: 320px;
    margin-top: 1px;
}



