
.model-list {
  width: 100%;
  height: 100%;
}
.model-list .brand-label {
    color: #A1A5B1;
    font-size: 12px;
    margin-right: 15px;
}
.model-list .img {
    width: 90px;
}
.model-list .desc {
    max-width: 600px;
    text-overflow: ellipsis;
    overflow: hidden;
}



